<template>
<div class="search">
    <b-row>
        <b-col cols="10">
        <b-list-group v-if="movies.length!=0">
            <b-list-group-item v-for="(movie,index) in movies" :key="index" class="vf-movie-item-list vf-only-bottom-border">
                <div class="vf-movie-poster">
                    <template v-if="movie.poster_path !=null">
                     <b-img :src="url_poster+'/'+movie.poster_path" alt="...">
                    </b-img>
                    </template>
                    <template v-else>
                        <b-icon icon="file-image-fill" font-scale="5"></b-icon>
                    </template>
                </div>
                <div class="vf-movie-information">
                    <template v-if="movie.review">
                        <b-button v-b-tooltip.hover title="Link to your review" class="app-bookmark">
                            <b-icon icon="bookmark"></b-icon>
                        </b-button>
                    </template>
                    <a class="h3 app-link-movie" @click="getMovie(movie.id)">{{movie.original_title}}</a>
                    <p class="mt-2"> Release date: {{movie.release_date}}</p>
                    <p class="mt-1"> Acting: 
                    <span v-for="(cast,index) in casting" :key="index"> 
                        <template v-if="movie.id === cast.movie_id">{{cast.name}} | </template>
                    </span>
                    </p>
                    <template v-if="token">
                        <div>
                        <WriteReview :movie_id="movie.id"/>
                        </div>
                    </template>
                </div>
            </b-list-group-item>   
        </b-list-group>
        </b-col>
    </b-row>
</div>
</template>

<script>

const api_url = process.env.VUE_APP_API_URL;
import WriteReview from '../components/shared//buttons/Write-review'

    export default{
        data(){
            return{
                movies: '',
                credits: '',
                url_poster : process.env.VUE_APP_API_POSTER,
                casting: [],
            }
        },
        components:{
            WriteReview
        },
        methods:{
            
            async searchMovieByQuery(query){
                    try{
                        this.movies = '';
                        this.casting = [];
                        await this.axios.get(`${api_url}/search/movie/${query}`,{
                            headers:{
                                authorization: this.token
                            }
                        }).then((response)=>{
                        this.movies = response.data.movies;
                        this.getCastFromMovie();
                        // console.log(response.data);
                    }).catch((error)=>{
                        console.log(error);
                    })
                
                    }catch(error){
                        console.log(error);
                    }
            },
            getCastFromMovie(){
                this.movies.forEach(movie =>{
                    movie.cast.slice(0,4).forEach(cast=>{
                        if(cast.known_for_department === 'Acting'){
                                cast.movie_id = movie.id;
                                this.casting.push(cast);
                        }
                    })
                })
            },
            
            getMovie(id){
                this.$router.push({path:`/movie/${id}`});
            },

            goReviewMovie(id){
                this.$router.push({path: `/review/movie/${id}`});
            }


    },
    created(){
        let query = this.$route.params.query;
        this.searchMovieByQuery(query);
    },
    mounted(){
        this.$root.$on('searchMovieByQuery', (query) =>{
            this.searchMovieByQuery(query);
        });
    },
    computed:{
        token : function() {return this.$store.getters.isAuth } 
    }
}



</script>

<style scoped>

.vf-movie-item-list:hover{
    background-color:rgba(243, 243, 243, 0.733);
}

.vf-movie-poster {
    width: 6em;
    height: inherit;
    float: left;
}

.vf-movie-poster img{
    width: inherit;
    height: inherit;
    object-fit: cover;
    padding: 0.2em;
}

.vf-movie-poster svg{
    margin-top:0.3em;
}


.vf-movie-information {
    margin-left: 7em;
    margin-top: 0.5em;
}

.app-bookmark {
    position: absolute;
    right: 0;
    background-color: #f1d18a;
    border: 0.1em solid #f1d18a;
}

.app-bookmark:hover {
    background-color: #f0bb49;
    border: 0.1em solid #f0bb49;
}

.app-bookmark:active {
    background-color: #f0bb49 !important;
    border: 0.1em solid #f0bb49 !important;
}

.app-bookmark:focus {
    background-color: #f0bb49;
    box-shadow: 0em 0em 0.2em 0.1em #f7d897 !important;
    border: 0.1em solid #f0bb49;
}

.app-link-movie {
    cursor:pointer;
}



</style>